// @flow
/* global window FileReader */

import React, { memo } from 'react';
import { map as _map, merge as _merge } from 'lodash';
import { Button } from '@material-ui/core';
import { AttachFile as AttachFileIcon } from '@material-ui/icons';
import PropTypes from 'prop-types';

const parseFileFormat = (file) => {
  return {
    // localTempId: uuidv4(),
    size: file.size,
    name: file.name,
    mimeType: file.type
    // data: file,
    // width: file.width,
    // height: file.height
  };
};

const getFilesData = (files) => {
  const promises = _map(files, file => {
    const parsedFile = parseFileFormat(file);
    const reader = new FileReader();

    return new Promise((resolve) => {
      reader.onload = (e) => {
        resolve(_merge(parsedFile, {
          url: e.target.result,
          data: e.target.result
        }));
      };
      reader.readAsDataURL(file);
    });
  });

  return Promise.all(promises);
};

const AttachButton = memo(({
  accept, onChange, buttonProps
}) => {
  const onClick = () => window.alert('Em breve esse recurso ser liberado');

  return (
    <React.Fragment>
      <Button
        color="primary"
        variant="outlined"
        size="large"
        onClick={onClick}
        startIcon={<AttachFileIcon />}
        {...buttonProps}
      >
        Anexar
      </Button>
      <input
        type="file"
        accept={accept}
        style={{ display: 'none' }}
        onChange={(e) => getFilesData(e.target.files).then(files => onChange(files))}
      />
    </React.Fragment>
  );
});
AttachButton.propTypes = {
  accept: PropTypes.string,
  onChange: PropTypes.func,
  buttonProps: PropTypes.object
};

export default AttachButton;
